<script>
	import { onMount, afterUpdate} from 'svelte';
	import { scaleOrdinal } from 'd3-scale';

	import * as d3 from 'd3';

	//Maps
	import MapUSA from './charts/map-usa.svelte'

	//Graphic Components
	import GraphicTitle from './components/GraphicTitle.svelte'
	import GraphicFooter from './components/GraphicFooter.svelte'

	//Helpers
	import * as colors from './helpers/colors.js'
	import { pastels } from './helpers/colors.js'

	import statehex from "./helpers/USStateHexbin.js";
	import * as test from '../public/datasets/test.json'

	import * as bannedLearning from '../public/datasets/banned-learning.json'
	import * as bannedLearningRedo from '../public/datasets/banned-learning-redo.json'


	let dataset;
	let selectoptions;
	let active;
	let colour;

	onMount(function() {
		selectoptions = ([...new Set(bannedLearning.default.map(
			function(d){
		
				return d.state;
		
			}



		))])

		active = selectoptions[0]
		dataset = bannedLearningRedo.default.filter(function(d){

			return d.state === active;
		})
		

	})

	afterUpdate(function() {

		dataset = bannedLearningRedo.default.filter(function(d){
		
			return d.state === active;
			
		})
	})


	export let width = Math.min(

		document.getElementById('svelte-app').getBoundingClientRect().width,
		900
	);

	function getorientation(w) {
		if (w > 750) {
			return "vertical"
		} else {
			return "horizontal"
		}
	}

	export let height = 400;

function chartHeight(x){
	if(window.innerWidth <= 400){

		return x/2;

	}
	else if(window.innerWidth <= 820){

			return x/1.25;

		}
	else{

		return x;
	}
}

	function handleMouseOver(e) {
	colour = 'green';
}
function handleMouseOut(e) {
	colour = 'red';
}

const main = document.querySelector('chart-container');
const toTopButton = document.querySelector('btt-button');

function obCallback(payload) {
  if (payload[0].isIntersecting === true) {
    toTopButton.classList.add('is-visible');
  } else {
    toTopButton.classList.remove('is-visible');
  }
}

if(main && toTopButton) {
  const ob = new IntersectionObserver(obCallback, {
    threshold: 0.2
  });
  ob.observe(main);
}



</script>

